import React from 'react'
import NewsCard from '../NewsCard/NewsCard'
import { Grid, Grow, Typography } from '@mui/material'
import useStyles from './style.js';
import Home from '../Home/Home';
import '../Home/styles.css'
const NewsCards = ({ articles }) => {
    const classes = useStyles();
    if (!articles.length) {
        return (
            <>
                <Home />
            </>
        )
    }
    return (
        <>
            <div style={classes.resultContainer}>
                <div className={`LogoContainer`}>
                    <img src="https://voicebot.ai/wp-content/uploads/2019/10/alan.jpg" alt="https://voicebot.ai/wp-content/uploads/2019/10/alan.jpg" className={`img`} />
                </div>
                <Grow in className={classes.box} style={{ marginTop: '5%', width: '97%' }}>
                    {/* <div className={`LogoContainer`}>
                    <img src="https://voicebot.ai/wp-content/uploads/2019/10/alan.jpg" alt="https://voicebot.ai/wp-content/uploads/2019/10/alan.jpg" className={`img`} />
                </div> */}
                    <Grid
                        container
                        className={classes.container}
                        alignItems='stretch'
                        spacing={3}
                    >
                        {articles.map((article, i) => (
                            <Grid
                                item
                                xs={12}
                                sm={6}
                                md={4}
                                lg={3}
                                style={{ display: 'flex' }}
                            >
                                <NewsCard
                                    article={article}
                                    i={i}
                                />
                            </Grid>
                        ))}
                    </Grid>
                </Grow>
            </div>
        </>
    )
}

export default NewsCards