import React, { useState } from 'react'
import Result from '../Result/Result'
import './styles.css'
const Home = () => {
    const [input, setInputValue] = useState('')
    const [Articles, setArticles] = useState([])
    const [style, setStyle] = useState('')
    const NEW_API_KEY = '5067b850f2c34800b1dac653e6e8a6de'
    const Clicked = async () => {
        const NEWS_API_URL = `https://newsapi.org/v2/everything?q=${input.toLowerCase()}&apiKey=${NEW_API_KEY}`
        const fetch_body = await fetch(NEWS_API_URL)
            // .then(response => response.json())
        const news = await fetch_body.json()
        setArticles(news.articles)
        setStyle('none')
    }
    return (
        <>
            <div className={`navbar`}>
                <div className={`navbarContainer`}>
                    <div className={`navbarBox`}>
                        <div className={`logo`}>
                            <span>ZER</span>
                            <span className={`redColor`}>0</span>
                            <span>NE</span>
                        </div>
                        <div className={`searchContainer`}>
                            <input type="text" name="search" id="" className={`inputFeild`}
                                placeholder='Search news'
                                onChange={e => setInputValue(e.target.value)} />
                            <i className={`searchIcon fa-solid fa-magnifying-glass`}
                                onClick={Clicked}
                            ></i>
                        </div>
                    </div>
                </div>
            </div>
            <div className={`HomeContainer`}>
                <div className={`LogoContainer`}>
                    <img src="https://voicebot.ai/wp-content/uploads/2019/10/alan.jpg" alt="https://voicebot.ai/wp-content/uploads/2019/10/alan.jpg" className={`img`} />
                </div>
                <div className={`intro`}>

                    <div className={`green card ${style}`}>

                        <h1>Latest News</h1>

                        <p>Try saying:
                            Give me the latest news
                        </p>
                    </div>
                    <div className={`blue card ${style}`}>

                        <h1>News by Categories</h1>

                        <p>Categories:
                            Business, Entertainment, General, Health, Science, Sports, Technology
                        </p>
                        <p>Try saying:
                            Give me the latest Technology news</p>
                    </div>
                    <div className={`purple card ${style}`}>
                        <h1>News by Terms</h1>
                        <p>Terms:
                            Donald Trump, BitCoin, PlayStation 5, Smartphones...
                        </p>
                        <p>Try saying:
                            What's up with PlayStation 5</p>
                    </div>
                    <div className={`darkblue card ${style}`}>
                        <h1>News by Sources</h1>
                        <p>Sources:
                            ABC News, Wired, BBC, Time, IGN, Buzzfeed, CNN...
                        </p>
                        <p>Try saying:
                            Give me the news from CNN</p>
                    </div>
                    {
                        Articles.map((article, i) => (
                            <Result 
                                article={article}
                                i={i}
                            />
                        ))
                    }
                </div>
            </div>
        </>
    )

}

export default Home
